.button{
    box-sizing: border-box;
    width: 100px;
    height: 70px;
    font-size: 30px;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 5px;
    border: none;
    transition: transform 350ms;
    background-color: #edffff;
}
.button:hover{
    transform: scale(1.05);
    cursor: pointer;
}